/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable no-restricted-globals */
/* eslint-disable camelcase */
import { sentenceCase,  } from 'change-case';
import { useState, useEffect, } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { useSnackbar } from 'notistack';
// @mui
import { useTheme } from '@mui/material/styles';
import {
  Card,
  Table,
  Avatar,
  Button,
  Checkbox,
  TableRow,
  TableBody,
  TableCell,
  Container,
  Typography,
  TableContainer,
  TablePagination,
  Switch,
} from '@mui/material';
// swal 
import swal from 'sweetalert';
// format
import FormControlLabel from '@mui/material/FormControlLabel';
// routes
import { PATH_ADMIN } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import Label from '../../components/Label';
import Iconify from '../../components/Iconify';
import Scrollbar from '../../components/Scrollbar';
import SearchNotFound from '../../components/SearchNotFound';

import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
// format number 
import { fCurrency,} from '../../utils/formatNumber';
// sections
import { UserListHead, AdexToolbar } from '../../sections/admin/user/list';


// axios

import axios from '../../utils/axios'; 

// ----------------------------------------------------------------------

const TABLE_HEAD = [
  { id: 'username', label: 'Username', alignRight: false },
  { id: 'wema', label: 'WEMA BANK', alignRight: false },
  { id: 'rolex', label: 'MONNIPOINT BANK', alignRight: false },
  {id: 'fed', label: 'FEDILITY BANK', alignRight: false},
  {id: 'sterlen', label: 'STERLING BANK', alignRight: false},
  {id: 'bal', label: 'Wallet Balance', alignRight: false},
  {id: 'refbal', label: 'Earning Balance', alignRight: false},
  {id: 'status', label: 'Account Status', alignRight: false}
];

// ----------------------------------------------------------------------

export default function AdexAutomated() {
  const theme = useTheme();
  const { themeStretch } = useSettings();
  const [userList, setUserList] = useState([]);
  const [page, setPage] = useState(0);
const [load,SetLoad] = useState(true);
const [totalPage, SetTotal] = useState(0);
const [selected, setSelected] = useState([]);
  const [filterName, setFilterName] = useState('');
  const [rowsPerPage, setRowsPerPage] = useState(5);
  const [dense, Setdense] = useState(false);
  const [isNotFound, SetNotFound] = useState(false);

  const { enqueueSnackbar } = useSnackbar();
  const AccessToken =  window.localStorage.getItem('accessToken');

  useEffect(()  => {
    initialize(page,rowsPerPage,filterName);
}, []);

const initialize = async (pag,adex = 5,search) => {
  let adex_page;
  if(pag > page){
     adex_page = pag + 1;
  }else{
     adex_page = pag;
  }
  try{
    SetLoad(true);

const data =  await axios.get(`/api/bank/info/all/bank/all/bank/${AccessToken}/secure?page=${adex_page}&adex=${adex}&search=${search}`)
  setUserList(data.data?.autobank?.data);
  SetLoad(false)
  SetTotal(data.data?.autobank?.total)     
  setPage(pag);
  SetNotFound(data.data?.autobank?.total === 0)
 }catch(_){
  SetLoad(false);
  setUserList([]);
 }
 }


  const handleSelectAllClick = (checked) => {
    if (checked.target.checked) {
      const newSelecteds = userList.map((n) => n.username);
      setSelected(newSelecteds);
      return;
    }
    setSelected([]);
  };

  const handleClick = (username) => {
    const selectedIndex = selected.indexOf(username);
    let newSelected = [];
    if (selectedIndex === -1) {
        newSelected = newSelected.concat(selected, username);
      } else if (selectedIndex === 0) {
        newSelected = newSelected.concat(selected.slice(1));
      } else if (selectedIndex === selected.length - 1) {
        newSelected = newSelected.concat(selected.slice(0, -1));
      } else if (selectedIndex > 0) {
        newSelected = newSelected.concat(
          selected.slice(0, selectedIndex),
          selected.slice(selectedIndex + 1),
        );
      }
    setSelected(newSelected);
  };

 const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    initialize(0,event.target.value,filterName)
    setPage(0);
  };
  
  const handleFilterByName = async(filterName) => {
    setFilterName(filterName);
    setPage(0);
    if(filterName){
      initialize(page,rowsPerPage,filterName);
      }else{
      initialize(page,rowsPerPage,filterName);
      SetNotFound(false)
      SetLoad(false);
     }
  };

  

  const handleDeleteMultiUser = (selected) => {
    const deleteUsers = userList.filter((user) => !selected.includes(user.name));
    setSelected([]);
    setUserList(deleteUsers);

    swal({
        title: "Note",
        text: `Are You Sure You Want To Delete this Automated Account Details`,
        icon: "info",
        buttons: true,
        dangerMode: true,
      })
      .then((willDelete) => {
        if (willDelete) {

            axios.post(`/api/delete/user/adex/automated/hacker/${AccessToken}/system`,{
                username:selected
            }).then(data => {
                enqueueSnackbar(data.data.message)
    const deleteUsers = userList.filter((user) => !selected.includes(user.username));
    setSelected([]);
    setUserList(deleteUsers);  
            }).catch(error => {
                if(error.data?.message){
                    enqueueSnackbar(error.data.message, { variant: 'error' })   
                }else{
                    enqueueSnackbar('An Error Occured', { variant: 'error' })   
                }
            });
        } else {
            swal("Request Cancelled");
          }
        });
  };




  function handleChangeDense(event){
   Setdense(event.target.checked);
  }

  return (
    <Page title="User: Automated Bank Account NUmber">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Automated Bank Account Number"
          links={[
            { name: 'Dashboard', href: PATH_ADMIN.root },
            { name: 'List' },
          ]}
          action={
            <Button
              variant="contained"
              component={RouterLink}
              to={PATH_ADMIN.user.newUser}
              startIcon={<Iconify icon={'eva:plus-fill'} />}
            >
              New User
            </Button>
          }
        />

        <Card>
          <AdexToolbar
            numSelected={selected.length}
            filterName={filterName}
            onFilterName={handleFilterByName}
            onDeleteUsers={() => handleDeleteMultiUser(selected)}
          />

          <Scrollbar>
            <TableContainer sx={{ minWidth: 800 }}>
              <Table
              size={dense ? 'small' : 'medium'}
              >
                <UserListHead
                  // order={order}
                  // orderBy={orderBy}
                  headLabel={TABLE_HEAD}
                  rowCount={userList.length}
                  numSelected={selected.length}
                  // onRequestSort={handleRequestSort}
                  onSelectAllClick={handleSelectAllClick}
                  onDeleteUsers={() => handleDeleteMultiUser(selected)}
                />

              {!load ?  <>  <TableBody>
                  {userList.map((row) => {
                    const { id, wema, username, rolex, sterlen, fed, bal, refbal,  status, profile_image  } = row;
                    const isItemSelected = selected.indexOf(username) !== -1;
                    return (
                      <TableRow
                        hover
                        key={id}
                        tabIndex={-1}
                        role="checkbox"
                        selected={isItemSelected}
                        aria-checked={isItemSelected}
                      >
                        <TableCell>
                          <Checkbox checked={isItemSelected} onClick={() => handleClick(username)} />
                        </TableCell>

                        <TableCell sx={{ display: 'flex', alignItems: 'center' }}>
                          <Avatar alt={username} src={profile_image} sx={{ mr: 2 }} />
                          <Typography>
                            {username}
                          </Typography>
                        </TableCell>
                        <TableCell align="left">{wema}</TableCell>              
                        <TableCell align="left">{rolex}</TableCell>
                        <TableCell align="left">{fed}</TableCell>
                        <TableCell align="left">{sterlen}</TableCell>
                        <TableCell align="left">₦{fCurrency(bal)}</TableCell>
                        <TableCell align="left">₦{fCurrency(refbal)}</TableCell>
                        <TableCell align="left">
                          <Label
                            variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                            color={(status === 0 && 'warning') || (status === 1 && 'success') || (status === 2 && 'error') || (status === 3 && 'warning')}
                          >
                            {sentenceCase((status === 0 && 'unverified') || (status === 1 && 'active') || (status === 2 && 'banned') || (status === 3 && 'deactivated'))}
                          </Label>
                        </TableCell>
                      </TableRow>
                    );
                  })}
               
                  
                </TableBody>  </> : <TableRow>
                      <TableCell align="center" colSpan={5} sx={{ py: 3 }}>
                      <Typography variant="body2">Loading ...</Typography>
                      </TableCell>
                    </TableRow> }
                {isNotFound && (
                    <TableRow>
                      <TableCell align="center" colSpan={5} sx={{ py: 3 }}>
                        <SearchNotFound searchQuery={filterName} />
                      </TableCell>
                    </TableRow>
                 
                )}
              </Table>
            </TableContainer>
          </Scrollbar>
          
          <FormControlLabel
        control={<Switch checked={dense} onChange={(event) => handleChangeDense(event)} />}
        label="Dense padding" align="left"
      />   
         
     
         <TablePagination
            rowsPerPageOptions={[5, 10, 25, 100]}
            component="div"
            count={totalPage}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={(e, page) =>  initialize(page,rowsPerPage,status,filterName)}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />

        </Card>
      </Container>
    </Page>
  );
}

// ----------------------------------------------------------------------

